.App {
    width: 100%;
    height: 100vh;

    display: flex;
    flex-direction: column;
    align-items: center;


    background-color: #2f74c0;
}


.heading {
    text-transform: uppercase;
    color: white;
    font-size: 40px;
    margin: 30px 0;
    z-index: 1;
    text-align: center;
}

@media (max-width: 800px) {
    .heading {
        margin: 15px 0;
        font-size: 35px;
    }
}